/*Start global*/
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-size: 10px;
  font-family: 'Roboto', sans-serif;
  color: #eee;
}

body{
  width: 100%;
  height: 100%;
  /* background: url("./assets/bluegrad.jpg") center no-repeat fixed; */
  background-color: #000000;
  background-image: linear-gradient(147deg, #000000 0%, #434343 74%);
  background-size: cover;
  position: relative;
}

body[data-aos-duration='4000'] [data-aos], [data-aos][data-aos][data-aos-duration='4000']{
  transition-duration: 4000ms;
}


a{
  text-decoration: none;
  color: #eee;
}

p{
  font-size: 1.8rem;
  font-weight: 300;
}

img {
  width: 100%;
}
/*End global*/

/* Start reusable */
.container{
  width: 90%;
  max-width: 120rem;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

.section-heading{
  text-align: center;
  margin-bottom: 10rem;
  padding: 20rem 0 0 0;
}

.section-heading h1{
  font-size: 3.5rem;
  color: rgba(255,255,255, .3);
  text-transform: uppercase;
  font-weight: 300;
  position: relative;
  margin-bottom: 1rem;
}

.section-heading h1::before,
.section-heading h1::after{
  content: '';
  position: absolute;
  bottom: -.5rem;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(255,255,255, 0.75);
}

.section-heading h1::before{
  width: 10rem;
  height: 3px;
  border-radius: .8rem;
}

.section-heading h1::after{
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 100%;
  bottom: -1rem;
}

.section-heading h6{
  font-size: 1.6rem;
  font-weight: 300;
}

.has-margin-right{
  margin-right: 5rem;
}
/* End reusable */


/* Header */
.header-section{
  width: 100%;
  height: 100vh;
  position: relative;
}

.top-nav {
  width: 100%;
  height: 100vh;
  top: -100vh;
  z-index: 50;
  position: fixed;
  background: linear-gradient(315deg, #ffa69e 0%, #5d4954 74%);
  border-bottom-right-radius: 100%;
  border-bottom-left-radius: 100%;
  transition: all 650ms cubic-bezier(1,0,0,1);
}

.nav-list{
  list-style: none;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-direction: column; */
}

li{
  margin: 2rem 2rem;
  font-size: 5rem;
  padding: 1rem
}

.nav-link{
  font-family: cursive, sans-serif;
}

.nav-link:hover,
.nav-link:focus{
  background: linear-gradient(to top, #ffe838, #fd57bf);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.top-nav-open{
  top: 0;
  border-radius: initial;
}

.menu-toggler{
  position: fixed;
  top: 5rem;
  right: 5rem;
  width: 5rem;
  height: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  z-index: 1500;
  transition: ease-out 650ms;
}

.menu-toggler-open{
  transform: rotate(-45deg);
}

.bar {
  background: linear-gradient(315deg, #ffa69e 0%, #5d4954 74%);
  width: 100%;
  height: 4px;
  border-radius: .8rem;
}

.bar.half {
  width: 50%;
}

.bar.start {
  transform-origin: right;
  transition: transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
}

.bar-start{
  transform: rotate(-450deg) translateX(.8rem);
}

.bar.end {
  align-self: flex-end;
  transform-origin: left;
  transition: transform 650ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
}

.bar-end{
  transform: rotate(-450deg) translateX(-.8rem);
}

.landing-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  z-index: 1;
}

.landing-text h1 {
  font-size: 13rem;
  font-family: cursive, sans-serif;
  /* background: linear-gradient(to top, #ffe838, #fd57bf); */
  background: linear-gradient(315deg, #ffa69e 0%, #5d4954 74%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding: 1rem;
  user-select: none;
}

.landing-text h6 {
  font-size: 2rem;
  font-weight: 300;
}

.nav-list p{
  cursor: pointer;
  font-size: 5rem;
}
/* End Header */

/* Start About */
.about .container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
}

.about-heading {
  text-align: center;
  text-transform: uppercase;
  line-height: 0;
  margin-bottom: 6rem;
}

.about-heading h1{
  font-size: 10rem;
  opacity: .3;
  color: #ffa69e;
}

.about-heading h6 {
  font-size: 2rem;
  font-weight: 300;
}

.profile-img{
  flex: 1;
  margin-right: 5rem;
  width: 100%;
}

.about-details{
  flex: 1;
  text-align: center;
}

.social-media{
  margin-top: 5rem;
}

.social-media i{
  font-size: 5rem;
  transition: color 650ms;
}

.fa-facebook-square:hover{
  color: #4267b2;
}

.fa-twitter-square:hover{
  color: #38a1f3;
}

.fa-instagram:hover{
  color: #ea4c89;
}
/* End About */

/* Start Services */

.my-skills{
  margin-top: 10rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-gap: 2.5rem;
  text-align: center;
}

.skill{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.skill h1{
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 2rem -2px 2rem 0;
}

.icon-container{
  width: 10rem;
  height: 10rem;
  border: 3px solid #5d4954;
  background-color: #ffa69e;
  margin-bottom: 2rem;
  display: flex;
  transform: rotate(45deg);
}

.icon-container i{
  color: #5d4954;
  font-size: 5rem;
  margin: auto;
  transform: rotate(-45deg);
}

/* End Services */


/* Start Portfolio */
.portfolio-item{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5rem;
}

.portfolio-item:last-child{
  margin-bottom: 0;
}

.portfolio-img{
  flex: 1;
  border-radius: 50%;
}

.portfolio-description{
  flex: 1;
}

.portfolio-description h6{
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 300;
  opacity: 0.3;
}

.portfolio-description h1{
  font-size: 3rem;
  font-weight: 300;
  margin: 1rem 0;
}

.portfolio-description .cta{
  display: inline-block;
  margin-top: 2.5rem;
  font-size: 1.5rem;
  text-transform: uppercase;
  color: #3883ff;
  transition: color 650ms;
  cursor: pointer;
}

.portfolio-description .cta:hover{
  color: #2f67cd;
}

/* End Portfolio */

/* Start Contact */
.contact{
  /* background-color: #221e3f; */
}

form{
  width: 70%;
  margin: 0 auto;
}

form label{
  font-size: 1.5rem;
}

input,
select,
textarea{
  width: 100%;
  padding: 1rem;
  margin-bottom: 5px;
  border: none;
  outline-color: #ffa69e;
}

input[type="submit"]{
  width: initial;
  background-color: #ffa69e;
  color: #eee;
  padding: 1rem 2rem;
  border-radius: .8rem;
  cursor: pointer;
  transition: background-color 650ms;
}

input[type="submit"]:hover{
  color: #221e3f;
  background-color: #2f67cd;
}

/* End Contact */

/* Start Footer */

.copyright{
  width: 100%;
  text-align: center;
  /* background-color: #16162d;   */
  padding: 2rem 0;
  margin-top: 5rem;
  position: relative;
}

.up{
  position: absolute;
  width: 5rem;
  height: 5rem;
  background-color: #eee;
  top: -2.5rem;
  right: 5rem;
  border-radius: 100%;
  display: flex;
  animation: pulse 2s infinite;
  cursor: pointer;
}

.up i{
  color: #16162d;
  font-size: 2rem;
  margin: auto;
}

.copyright p{
  font-size: 1.4rem;
}

/* Emd Footer */

/* Start Animations */

@keyframes pulse{
  0%{
    /* box-shadow: 0 0 0 0 rgba(253, 87, 191, 0.99); */
    box-shadow: 0 0 0 0 #ffa69e;
  }
  70%{
    box-shadow: 0 0 0 2rem rgba(253, 87, 191, 0);
  }
  100%{
    box-shadow: 0 0 0 0 rgba(253, 87, 191, 0);
  }
}

/* End Animations */

/* Media Queries */
@media screen and (max-width: 768px){
  body{
    /* background: url("./assets/sitebg2.jpg") center no-repeat fixed;
    background-size: cover; */
    width: 100%;
    height: 100%;
    /* background: url("./assets/bluegrad.jpg") center no-repeat fixed; */
    background-color: #000000;
    background-image: linear-gradient(147deg, #000000 0%, #434343 74%);
  }
  
  .landing-text h1 {
    font-size: 6rem;
  }

  .landing-text h6 {
    font-size: 1.7rem;
  }

  .nav-list {
    flex-direction: column;
  }

  .li{
    margin: 2rem 0;
  }

  .nav-link{
    font-size: 4.5rem;
  }

  .about-heading h1 {
    font-size: 8rem;
  }

  .section-heading h1{
    font-size: 3rem;
  }

  .about-details .nav-list{
    flex-direction: row;
  }

  .about-details li{
    margin: 0 2rem;
  }

  .icon-container{
    width: 7rem;
    height: 7rem;
  }
 
}

@media screen and (max-width: 576px) {

  .profile-img{
    width: 50%;
  }

  .menu-toggler{
    top: 4rem;
    right: 12rem;
  }

  .landing-text h1{
    font-size: 8rem;
  }

  .landing-text h6{
    font-size: 1.4rem;
  }

  .nav-link{
    font-size: 3.5rem;
  }

  .about .container {
    flex-direction: column;
  }

  .profile-img{
    margin: 0 0 7rem 0;
  }

  .portfolio-item{
    flex-direction: column;
  }

  .portfolio-img {
    margin: 0 0 2rem 0;
    order: 1;
  }

  .portfolio-description {
    margin: 0;
    order: 2;
  }

  form{
    width: 100%;
  }

  .up{
    right: 2rem;
  }
}
/* Media Queries */